import React from 'react';
import {NavLink, Modal, ModalHeader, ModalBody} from 'reactstrap';
import {Link} from 'react-router-dom';
import DropDown from './DropDownComponent';

class HeaderBig extends React.Component{
    
    constructor(props){
        super(props);
        this.state = {
            electronics : () =>{
                return(
                    null
                );
            },
            men : () => {
                return(
                    null
                );
            },
            women : () => {
                return(
                    null
                );
            },
            kids : () => {
                return(
                    null
                );
            },
            sports : () => {
                return(
                    null
                );
            },
            books : () => {
                return(
                    null
                );
            },
            userInfo: () => {
                return(
                    null
                );
            },
            userInfoStatus: 0,                      
        }       
    }   

    avatarClicked = () => {
        if(this.state.userInfoStatus !== 0){
            this.setState({
                userInfo: () => {
                    return(
                        null                                   
                    );
                },
                userInfoStatus: 0
            })
        }
        else{
            var name = this.props.firstname + " " + this.props.lastname;
            this.setState({
                userInfo: () => {
                    return(
                        <div style={{backgroundColor:"#000000"}}>
                            <p style={{width:"100%", fontSize:"13px", color:"cyan", textAlign:"center"}}>{name}</p>
                            <div style={{width:"100%"}}><NavLink onClick={() => {
                                var empty = "";
                                localStorage.setItem("logged", JSON.stringify(empty));
                                window.location.reload(false);
                            }}><Link style={{textDecoration:"none"}}>Log out</Link></NavLink></div>
                            <div><NavLink style={{width:"100%"}}><Link style={{textDecoration:"none"}} to="/cart">My Cart</Link></NavLink></div>
                            <div style={{width:"100%"}}><NavLink><Link style={{textDecoration:"none"}} to="/wishlist">My WishList</Link></NavLink></div>
                            <div style={{width:"100%"}}><NavLink><Link style={{textDecoration:"none"}} to="/orders">My Orders</Link></NavLink></div>
                        </div>                                    
                    );
                },
                userInfoStatus: 1
            })
        }
    }

    loginOption = () => {
        if(this.props.firstname.length === 0){
            return(
                <NavLink outline className="basic-font nav-link">
                    <Link onClick={() => {
                        return(
                            this.props.loginClicked()
                        );
                    }}>
                        <span className="fa fa-sign-in fa-lg"></span> Login
                    </Link>                                    
                </NavLink>
            );
        }
        else{           
            return(
                <div onMouseEnter={() => {                        
                    this.avatarClicked();
                }} onMouseLeave={() => {
                    this.avatarClicked();
                }}>                
                    <NavLink outline className="basic-font nav-link">                    
                        <div style={{height:"45px", width: "45px", borderRadius:"50%"}}>
                            <img src='data:image/jpeg;base64,'
                            style={{width:"100%", height:"100%", borderRadius:"50%", objectFit:"fit"}}></img>                                                                     
                        </div>
                    </NavLink>
                    <div style={{marginTop:"0px",backgroundColor:"floralwhite", width:"max-width", zIndex:"7", position:"absolute"}}>
                        {this.state.userInfo()}
                    </div>  
                </div>
            );
        }        
    }   
    
    render(){        
        const called = (num) => {                       
            return(
                <DropDown number={num}/>
            );        
        }        

        return(
        <nav style={{height:"fit-content", backgroundColor:"black", userSelect:"none"}}>                            
            <div className="row" style={{width:"100%"}}>
                <div style={{margin:"auto"}}>
                    <Link to='/emart' style={{textDecoration:"none"}}>
                        <h5 className="basic-font" style={{marginLeft:"15px"}}>EM</h5>
                    </Link>                        
                </div>                
                <div className="container" style={{paddingTop:"10px", paddingBottom:"10px"}}>             
                    <div className="row">                                    
                        <div style={{marginLeft:"10px"}}>
                            <div onMouseLeave={() => {                                
                                    return(this.setState({
                                        electronics: null
                                    }))                               
                            }}onMouseEnter={() => {                                
                                return( 
                                    this.setState({
                                        electronics: called(0)
                                    }) 
                                );
                                }}>  
                                <NavLink>
                                    <Link style={{textDecoration:"none"}}>
                                        <h6 className="basic-font"><span className="fa fa-plug"></span> Electronics</h6>
                                    </Link>
                                </NavLink>                                                                                                                      
                                <div style={{marginTop:"0px",backgroundColor:"floralwhite", width:"max-content", zIndex:"7", position:"absolute"}}>
                                    {this.state.electronics}
                                </div>                                               
                            </div>                                                                                                                                           
                        </div>                     
                        <div style={{marginLeft:"10px"}}>
                            <div onMouseLeave={() => {                                
                                        return(this.setState({
                                            men: null
                                        }))                               
                                }}onMouseEnter={() => {                                
                                    return( 
                                        this.setState({
                                            men: called(1)
                                        }) 
                                    );
                                    }}> 
                                <NavLink>
                                    <Link style={{textDecoration:"none"}}>
                                        <h6 className="basic-font"><span className="fa fa-male"></span> Men</h6>
                                    </Link>
                                </NavLink>                                                                                                                       
                                <div style={{marginTop:"0px",backgroundColor:"floralwhite", width:"max-content", zIndex:"6", position:"absolute"}}> 
                                    {this.state.men}        
                                </div>                 
                            </div>                                                                                                                   
                        </div>                     
                        <div style={{marginLeft:"10px"}}>
                            <div onMouseLeave={() => {                                
                                        return(this.setState({
                                            women: null
                                        }))                               
                                }}onMouseEnter={() => {                                
                                    return( 
                                        this.setState({
                                            women: called(2)
                                        }) 
                                    );
                                    }}>
                                <NavLink>
                                    <Link style={{textDecoration:"none"}}>
                                        <h6 className="basic-font"><span className="fa fa-female"></span> Women</h6>
                                    </Link>
                                </NavLink>                                                                                                                                    
                                <div style={{marginTop:"0px",backgroundColor:"floralwhite", width:"max-content", zIndex:"5", position:"absolute"}}> 
                                    {this.state.women}        
                                </div>                  
                            </div>
                        </div>                                                                                                                                    
                        <div style={{marginLeft:"10px"}}>
                            <div onMouseLeave={() => {                                
                                        return(this.setState({
                                            kids: null
                                        }))                               
                                }}onMouseEnter={() => {                                
                                    return( 
                                        this.setState({
                                            kids: called(3)
                                        }) 
                                    );
                                    }}> 
                                <NavLink>
                                    <Link style={{textDecoration:"none"}}>
                                        <h6 className="basic-font"><span className="fa fa-child"></span> Kids</h6>
                                    </Link>
                                </NavLink>                                                                                                                                    
                                <div style={{marginTop:"0px",backgroundColor:"floralwhite", width:"max-content", zIndex:"4", position:"absolute"}}> 
                                    {this.state.kids}        
                                </div>                  
                            </div>
                        </div>                                                                                                                                    
                        <div style={{marginLeft:"10px"}}>
                            <div onMouseLeave={() => {                                
                                        return(this.setState({
                                            sports: null
                                        }))                               
                                }}onMouseEnter={() => {                                
                                    return( 
                                        this.setState({
                                            sports: called(4)
                                        }) 
                                    );
                                    }}> 
                                <NavLink>
                                    <Link style={{textDecoration:"none"}}>
                                        <h6 className="basic-font"><span className="fa fa-futbol-o"></span> Sports</h6>
                                    </Link>
                                </NavLink>                                                                                                                                  
                                <div style={{marginTop:"0px",backgroundColor:"floralwhite", width:"max-content", zIndex:"3", position:"absolute"}}> 
                                    {this.state.sports}        
                                </div>                   
                            </div>
                        </div>                                                                                                                                    
                        <div style={{marginLeft:"10px"}}>
                            <div onMouseLeave={() => {                                
                                        return(this.setState({
                                            books: null
                                        }))                               
                                }}onMouseEnter={() => {                                
                                    return( 
                                        this.setState({
                                            books: called(5)
                                        }) 
                                    );
                                    }}> 
                                <NavLink>
                                    <Link style={{textDecoration:"none"}}>
                                        <h6 className="basic-font"><span className="fa fa-book"></span> Books</h6>
                                    </Link>
                                </NavLink>                                                                                                                                   
                                <div style={{marginTop:"0px",backgroundColor:"floralwhite", width:"max-content", zIndex:"2", position:"absolute"}}> 
                                    {this.state.books}       
                                </div>                   
                            </div>
                        </div>                                                                        
                    </div>                                                                                                                                                                  
                </div>
                <div style={{margin:"auto"}} id="loginOption">   
                    {this.loginOption()}
                </div>                 
            </div>                                                                                                                          
        </nav>
        );
    }
}
export default HeaderBig;